import React from 'react';
import linkedInIMG from './images/social-icons/linkedin.png'
import gitHubIMG from './images/social-icons/github.png'

const Footer = () => {
    return (
        <>
            <footer className="text-muted" style={{padding: '25px'}}>
                <div className="container">
                    <p className="float-right">
                        <img href="https://www.linkedin.com/in/ethan-m-carlsson-/" src={linkedInIMG} style={{height: '24px', width: '24px'}} alt="LinkedIn Icon"/>
                        <img href="https://www.linkedin.com/in/ethan-m-carlsson-/" src={gitHubIMG} style={{height: '24px', width: '24px'}} alt="Github Icon"/>
                    </p>
                    <p>If you're interested in my work you can contact me at ethanmcarlsson[at]gmail.com.</p>
                </div>
            </footer>
        </>
    )

}

export default Footer