import './Portfolio.css';
import React from 'react';
import snakeElephant from './images/portfolio-images/snakeElephant.jpg';
import worksheetIMG from './images/portfolio-images/worksheet.jpg';
import treeDisputeHelperIMG from './images/portfolio-images/treeDisputeHelper.jpg';
// import onlineClassroomsIMG from './images/portfolio-images/classroomApp.jpg';
import starWarsIMG from './images/portfolio-images/starWars.jpg';


// Technology icons
import pythonIMG from './images/technology-icons/python.png';
import djangoIMG from './images/technology-icons/django.png';
import postgresIMG from './images/technology-icons/postgres.png';
import htmlIMG from './images/technology-icons/html.png';
import cssIMG from './images/technology-icons/css.png';
import jsIMG from './images/technology-icons/javascript.png';
import bootstrapIMG from './images/technology-icons/bootstrap.png';
import s3IMG from './images/technology-icons/s3.png';
import herokuIMG from './images/technology-icons/heroku.png';
import jqueryImg from './images/technology-icons/jquery.png';
import figmaIMG from './images/technology-icons/figma.png';

const projects = [
    {
        id: 1,
        name: 'Your Online Worksheets',
        link: 'https://worksheetsonline.herokuapp.com/',
        technologies: [pythonIMG, djangoIMG, postgresIMG, htmlIMG, cssIMG, jsIMG, bootstrapIMG, herokuIMG],
        summary: `
        A web app I built to help deliver online classes and homework. The user copies and pastes an already existing fill in the blank style worksheet and transforms the blanks into text inputs. 
        `,
        img: worksheetIMG,
        github: 'https://github.com/ethancarlsson/worksheetsonline'

    },
    {
        id: 2,
        name: 'Tree Dispute Helper',
        link: 'http://www.treedisputehelper.com/',
        technologies: [pythonIMG, djangoIMG, postgresIMG, htmlIMG, cssIMG, jsIMG, bootstrapIMG, herokuIMG, s3IMG],
        summary: `
        A web app designed to assist self represented litigants in Tree Disputes. The user fills out the web form and the program writes on a word document to indicate which areas on the legal form they will need to complete, simulating the early stages of a legal interview.
        `,
        img: treeDisputeHelperIMG,
        github: 'https://github.com/ethancarlsson/tree_disputes_form'

    },
    {
        id: 3,
        name: 'Psycopg2-connection',
        link: 'https://pypi.org/project/psycopg2-connection/',
        technologies: [pythonIMG],
        summary: `
        A python package I created as part of a larger project to abstract away connections to postgres. The idea is to create the feeling that you can write SQL code directly into python without having to worry about opening and closing connections.
        `,
        img: snakeElephant,
        github: 'https://github.com/ethancarlsson/psycopg2-connection'
    },

    {
        id: 4,
        name: 'Star Wars Bot Chat',
        link: 'https://ethancarlsson.github.io/star-wars-bot/',
        technologies: [jsIMG, htmlIMG, cssIMG, jqueryImg, figmaIMG],
        summary: `
        A fairly silly app that lets you have a conversation with a robot that likes to talk about Star Wars. The primary purpose of the app was to practice using front end technologies and Figma. The bot's replies are random comments taken from the reddit api. 
        `,
        img: starWarsIMG,
        github: 'https://ethancarlsson.github.io/star-wars-bot/'
    },

];



const Portfolio = () => {
    return(
        <>
            {
                projects.map(
                    (project, index) => (
                    <div className="card mb-3" key={index}>
                    <div className="row no-gutters">
                        <div className="col-md-4">
                            <a href={project.link}><img className="card-img" src={project.img} alt={project.name} width="490" height="300"/></a>
                        </div>
                        <div className="col-md-8">
                            <div className="card-body">
                                <h5 className="card-title">{project.name}</h5>
                                {project.technologies.map(
                                (technology, index) => (
                                    <img src={technology} key={index} alt={'technology icon'} style={{height: '24px', width: '24px'}}/>
                                )
                                )}
                                <p className="card-text">{project.summary}</p>
                                <p>
                                    Github: <a href={project.github}>{project.github}</a>
                                </p>
                                
                                
                                </div>
                                </div>
                                </div>
                                
                        </div>)
                )}


        </>
    )
}

export default Portfolio