import './App.css';
import React from 'react';

import pythonImg from './images/technology-icons/python.png';
import cssIMG from './images/technology-icons/css.png';
import djangoImg from './images/technology-icons/django.png';
import htmlImg from './images/technology-icons/html.png';
import javascriptImg from './images/technology-icons/javascript.png';
import postgresImg from './images/technology-icons/postgres.png';
import reactImg from './images/technology-icons/react.png';
import jqueryImg from './images/technology-icons/jquery.png';

const About = () => {
    return(
      <div className="container">
          <div className="row">
              <div className="col-md">
                <h2>I'm a web developer currently based in Barcelona  </h2>
                <p>I spend most of my time in the back-end solving problems with python, but I have a design streak and I'm always interested in making a great user experience with web technologies.</p>
                <p>
                  I have experience as an English teacher in Spain, and in a previous life I got a degree of laws in Australia, volunteered in community law and worked in the Land and Environment Court of NSW. 
                  My personal projects often draw from this background and I hope to use my newfound programming skills to bring value to education and improve access to justice in the areas of law I used to work in and study. 
                </p> 
              </div>
              <div className="col-md">
                  <h2>Technologies</h2>
                    <ul className="list-group list-group-flush">
                        <li className="list-group-item"><img src={pythonImg} style={{height: '24px', width: '24px'}} alt="python"/> Python</li>
                        <li className="list-group-item"><img src={postgresImg} style={{height: '24px', width: '24px'}} alt="postgres"/> SQL</li>
                        <li className="list-group-item"><img src={djangoImg} style={{height: '24px', width: '24px'}} alt="Django"/> Django</li>
                        <li className="list-group-item"><img src={javascriptImg} style={{height: '24px', width: '24px'}} alt="JavaScript"/> JavaScript</li>
                        <li className="list-group-item"><img src={htmlImg} style={{height: '24px', width: '24px'}} alt="HTML"/> HTML</li>
                        <li className="list-group-item"><img src={cssIMG} style={{height: '24px', width: '24px'}} alt="css"/> css</li>
                        <li className="list-group-item"><img src={reactImg} style={{height: '24px', width: '24px'}} alt="React"/> React</li>
                        <li className="list-group-item"><img src={jqueryImg} style={{height: '24px', width: '24px'}} alt="React"/> jQuery</li>
                    </ul>

              </div>
          </div>
      </div>)};

  
export default About