import React from 'react';
import './App.css'
import './buttons.css';


const NavBar = ( {activeTab, onTabChange} ) => {
    const itemClass = tabName =>
        `nav-item ${
            activeTab === tabName ? 'active': ''
        }`

    return(
        <div className="App-nav">
            
        <nav className="navbar navbar-expand-lg navbar-light">
            <ul className="navbar-nav">
                        <li className={itemClass('about')}>
                            <button onClick={() => onTabChange('about')} className="btn nav-link"><h6>About me</h6></button>
                        </li>
                        <li className={itemClass('portfolio')}>
                            <button onClick={() => onTabChange('portfolio')} className="btn nav-link"><h6>Portfolio</h6></button>
                        </li>
                        <li className={itemClass('contact')}>
                            <button onClick={() => onTabChange('contact')} className="btn nav-link"><h6>Contact</h6></button>
                        </li>

                </ul>

                </nav>
                </div>
    )
}

export default NavBar