import React from 'react';
import './bootstrap-4.5.3-dist/css/bootstrap.min.css';
import { useEffect } from 'react';



const Title = () => {

    useEffect(() => {
        const canvas = document.getElementById('Name-canvas')
        const ctx = canvas.getContext('2d');
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight - 9300;
        let particleArray = [];
        


        let xAdjust = 0
        let yAdjust = 0


        
        const mouse = {
            x: null,
            y: null,
            radius: 40,
        }
        
        window.addEventListener('mousemove', function(event){
            mouse.x = event.x;
            mouse.y = event.y;
        });
        
        
        ctx.fillStyle = 'black';
        if (window.innerWidth < 500) {
            canvas.height = 200  

            ctx.font = `${18}px Helvetica`;
            ctx.fillText('Ethan', 5, 30);    
            ctx.fillText('Carlsson', 25, 60);
        } else {
            ctx.font = `${window.innerWidth /100 + 16}px Helvetica`;
            ctx.fillText('Ethan Carlsson', 25, 40);    
        }
        const textCoordinates = ctx.getImageData(0, 0, 225, 60);

        class Particle {
            constructor(x, y) {
                this.x = x;
                this.y = y;
                this.size = 1.5;
                this.baseX = this.x;
                this.baseY = this.y;
                this.density = (Math.random() * 30) +1;
            }
            draw() {
                ctx.fillStyle = 'black';
                ctx.beginPath();
                ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2);
                ctx.closePath();
                ctx.fill()
            }
            update() {
                let dx = mouse.x - this.x;
                let dy = mouse.y - this.y;
        
                let distance = Math.sqrt(dx*dx + dy*dy);
                let forceDirectionX = dx/distance;
                let forceDirectionY = dy/distance;
                let maxDistance = mouse.radius;
                let force = (maxDistance - distance) / maxDistance;
                let directionX = forceDirectionX * force * this.density;
                let directionY = forceDirectionY * force * this.density;
                
                if (distance < mouse.radius) {
                    this.x -= directionX;
                    this.y -= directionY;
        
                } else {
                    if (this.x !== this.baseX) {
                        let dx = this.x - this.baseX;
                        this.x -= dx/10;
                    }
                    if (this.y !== this.baseY) {
                        let dy = this.y - this.baseY;
                        this.y -= dy/10;
                    }
                }
        
            }
        }
        
        function init() {
            particleArray = [];
            for (let y = 0, y2 = textCoordinates.height; y < y2; y++) {
                for (let x = 0, x2 = textCoordinates.width; x < x2; x++) {
                    if (textCoordinates.data[(y * 4 * textCoordinates.width) + (x * 4) + 3] > 128) { //skip elements so that only the opacity is chaced within the array. Too complicated, but it speeds up the program a lot.
                        let positionX = x + xAdjust;
                        let positionY = y + yAdjust;
                        particleArray.push(new Particle( //particle distance.
                            positionX * 3,
                            positionY * 3
                            ))
                    }
                }
            }
        }
        
        init();
        
        function animate() {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            for (let i = 0; i < particleArray.length; i++){
                particleArray[i].draw();
                particleArray[i].update()
            }
            requestAnimationFrame(animate);
        }
        animate();

    }, [])

    return (
        <>
            <canvas id="Name-canvas" className="Name-canvas"></canvas>
        </>
    )
}

export default Title